    background: #fff;
    margin-top: .15rem;
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    ul{
        width: 6.9rem;
        margin-left: .3rem;
        color: #333;
        font-size: .3rem;
        li{
            height: .9rem;
            p{
