                line-height: .9rem;
                text-align: right;
                display: inline-block;
            }
        }
    }
    .ulTop{
        border-bottom: 1px solid #eaeaea;
    }
    .ulBottom{
        width: 6.9rem;
        div{
            margin-top: .2rem;
            padding-bottom: .3rem;
            p{
                display: inline;            
            }
            .fontStyle{
                /*display: inline;*/
                width: 6.9rem;
                line-height: .5rem;
            }
        }
    }
