<template>
    <div class="promotion">
      <div class="nav clearfix">
        <img class="navBack fl" src="../../../static/images/common/back.png" alt="" @click="navBack()">
        <h2 class="navTitle fl"></h2>
      </div>
      <div class="content">
        <div class="code">
          <canvas id="canvas1"></canvas>
        </div>
        <div class="msg">
          <h3></h3>
          <p>
            </p>
        </div>
        <div class="tel">400-8988-808</div>
      </div>
    </div>
</template>

<script>
  import QRCode from 'qrcode'
    export default {
        name: "",
        data() {
            return {
              QueryDetail: "http://"+window.location.host + "/promotionLogin",
              referrerId: "",
              referrerIdH5:"",
              cityId:""
            }
        },
      mounted(){
        this.referrerIdH5 =  getUrlId("referrerId");
        this.cityId =  getUrlId("cityId");
        if(getUrlId("move")){
          $(".nav").hide();
          $(".content").css({margin:0})
        }
        if(this.referrerIdH5){
          this.useqrcodeH5()
        }else{
          this.init();
        }
        this.fenxiangFun();
      },
      beforeRouteEnter (to, from, next) {
        var u = navigator.userAgent;
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        // XXX: iOSHTML5 History
        if (isiOS && to.path !== location.pathname) {
          // location.replace
          location.assign(to.fullPath)
        } else {
          next()
        }
      },
        methods: {
          navBack() {
              this.$router.go(-1)
          },
          init(){
            var that = this;
            this.$http({
              url: this.changeData() + "/member/auth/createQRCode",
              method: 'post',
              params: {
                type: 1 //1
              }
            }).then(res => {
              console.log(res)
              if(res.data.code == "101"){
                this.referrerId = res.data.referrerId;
                console.log( this.referrerId)
                that.useqrcode();
              }
            }).catch(err => {
              console.log(err)
            });
          },
          useqrcode(){//
            this.QueryDetail = this.QueryDetail+"?referrerId="+this.referrerId+"&cityId="+sessionStorage.cityId;
            var canvas = document.getElementById('canvas1')
            QRCode.toCanvas(canvas, this.QueryDetail, function (error) {
              if (error) console.error(error)
              console.log('success!');
            })
          },
          useqrcodeH5(){//
            this.QueryDetail = this.QueryDetail+"?referrerId="+this.referrerIdH5+"&cityId="+this.cityId;
            var canvas = document.getElementById('canvas1')
            QRCode.toCanvas(canvas, this.QueryDetail, function (error) {
              if (error) console.error(error)
              console.log('success!');
            })
          },
          fenxiangFun(){//
            var loginUrl = window.location.href.replace('/promotion', "/promotionLogin?referrerId="+this.referrerId+"&teamId="+this.teamId+"&cityId="+sessionStorage.cityId)
            this.$http({
              url:this.changeData() + '/member/weChatShare',
              method: "post",
              params: {
                url : window.location.href.split('#')[0]
              }
            }).then(function(res){
              console.log(res)
              wx.config({
                //debug: true, // ,apialertpclogpc
                appId: res.data.appId, // 
                timestamp: res.data.timestamp, // 
                nonceStr: res.data.nonceStr, // 
                signature: res.data.signature,// 
                jsApiList: ["onMenuShareAppMessage","onMenuShareTimeline"] // JS
              });

              wx.ready(function(){
                // configreadyconfigconfigreadyready
                //
                wx.onMenuShareAppMessage({
                  title: "", // 
                  desc: '', // 
                  link: loginUrl, // JS
                  imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11539667606_.pic.jpg", // 
                  type: '', // ,musicvideolinklink
                  dataUrl: '', // typemusicvideo
                  success: function () {
                    // 
                  }
                });

                //
                wx.onMenuShareTimeline({
                  title: "", // 
                  link: loginUrl, // JS
                  imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11539667606_.pic.jpg", // 
                  success: function () {
                    // 
                  }
                });
              });
              wx.error(function(res){
                // configerrorconfigdebugresSPA
              });
            }.bind(this)).catch(function(err){
              console.log("",err)
            });
          },
        }
    }
</script>

<style lang="scss" type="text/scss" scoped>
  @import "../../../static/css/nav.css";
  .promotion {
    width: 7.5rem;
    margin: 0 auto;
    .content{
      margin-top: .9rem;
      height: 12.06rem;
      background: url(../../../static/images/code/code-bg.png) no-repeat;
      -webkit-background-size: 7.5rem 12.06rem;
      background-size: 7.5rem 12.06rem;
      .code{
        padding-top: 2.8rem;
        width: 4.1rem;
        height: 4.1rem;
        margin: 0 auto;
        #canvas1{
          width: 4rem!important;
          height: 4rem!important;
          box-shadow: 0 0 0 .01rem #898989;
        }
      }
      .msg{
        margin-top: 1.4rem;
        text-align: center;
        h3{
          font-size: .36rem;
          color: #fff;
          margin-bottom: .3rem;
        }
        p{
          color: #fff;
          font-size: .28rem;
          line-height: .5rem;
          text-align: justify;
          padding: 0 .9rem;
          span{
            display: inline-block;
            vertical-align: middle;
            width: .8rem;
            height: .35rem;
            line-height: .4rem;
            text-align: center;
            border-radius: .05rem;
            border: 0.01rem solid #FFC54A;
            color: #FFC54A;
          }
        }
      }
      .tel{
        margin-top: 1.2rem;
        font-size: .28rem;
        color: #fff;
        text-align: center;
      }
    }
  }
</style>



