<template>
    <div class="newsDetails">
        <div class="nav clearfix">
            <img class="navBack fl" src="../../../static/images/common/back.png" alt="" @click="navBack()">
            <h2 class="navTitle fl"></h2>
        </div>
        <iframe class="iframe" name="iframe" :src="this.newsUrl" frameborder="0" scrolling="auto"></iframe>
    </div>
</template>
<script>
import $ from '../../../static/js/jquery.min.js';
import axios from 'axios';
import qs from 'qs';
export default {
name: "newsDetails",
    data () {
        return {
            typeUlr: "",
            newsUrl: ""
        }
    },
    computed:{

    },
    beforeRouteEnter (to, from, next) {
        var u = navigator.userAgent;
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
        // XXX: iOSHTML5 History
        if (isiOS && to.path !== location.pathname) {
            // location.replace
            location.assign(to.fullPath)
        } else {
            next()
        }
    },
    methods:{
        // 
        navBack(){        
            if(this.typeUlr == "news"){
                this.$router.push({path: '/news'}); 
            }else{
                this.$router.push({path: '/home'}); 
            }   
            // this.$router.push({path: '/home'}         
        }, 
        fenxiangFun(){//
            // console.log(window.location.href.split('#')[0])
            var that = this;
            this.$http({
                url:this.changeData() + '/member/weChatShare',
                method: "post",
                params: {
                    url: window.location.href.split('#')[0]  
                }
            }).then(function(res){
                // console.log(res)
                wx.config({
                    debug: false, // ,apialertpclogpc
                    appId: res.data.appId, // 
                    timestamp: res.data.timestamp, // 
                    nonceStr: res.data.nonceStr, // 
                    signature: res.data.signature,// 
                    jsApiList: ["onMenuShareAppMessage","onMenuShareTimeline"] // JS
                });

                //
                wx.ready(function(){
                    // configreadyconfigconfigreadyready
                    //
                    wx.onMenuShareAppMessage({
                        title: that.newsObj.title, // 
                        desc: '', // 
                        link: window.location.href.split('#')[0], // JS
                        imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11539667606_.pic.jpg", // 
                        type: '', // ,musicvideolinklink
                        dataUrl: '', // typemusicvideo
                        success: function () {
                            // 
                        }
                    });

                    //
                    wx.onMenuShareTimeline({
                        title: that.newsObj.title, // 
                        link: window.location.href.split('#')[0], // JS
                        imgUrl: "https://up-static.oss-cn-beijing.aliyuncs.com/Config/11539667606_.pic.jpg", // 
                        success: function () {
                            // 
                        }
                    });
                });
                wx.error(function(res){
                    // configerrorconfigdebugresSPA
                });
            }.bind(this)).catch(function(err){
              console.log("",err)
            });
        },        
    },
    mounted() {
        //Url
        function GetRequest() {
            var url = location.search; //url"?"
            var theRequest = new Object();
            if (url.indexOf("?") != -1) {
                var str = url.substr(1);
                var strs = str.split("&");
                for (var i = 0; i < strs.length; i++) {
                    theRequest[strs[i].split("=")[0]] = decodeURI(strs[i].split("=")[1]);
                }
            }
            return theRequest;
        }
        var request = new Object();
        request = GetRequest();
        this.typeUlr = request['url']; 
        this.newsUrl = decodeURIComponent(this.typeUlr); 
        $(".iframe").height($(window).height());        
    },
    updated(){
        // 
        this.fenxiangFun();
    }
}
</script>

<style lang="scss" type="text/scss" scoped>
@import "../../../static/css/commonNav.css";
.newsDetails{
    width: 7.5rem;
    background: #fff;
    /*height: 100vh;*/
    margin: 0 auto;
}
.newsDetailsWrap{
    width: 6.9rem;
    padding: .3rem;
    padding: 1.2rem .3rem .3rem .3rem;
    .newsDetailsTop{
        width: 6.9rem;
        background: #fff;
        h2{
            color: #333333;
            font-size: .34rem;
        }        
        ul{
            width: 6.9rem;
            height: .6rem;
            position: relative;
            color: #898989;
            font-size: .26rem;
            margin-top: .2rem;
            li{
                float: left;
                img{
                    width: .28rem;
                    height: .18rem;
                    position: relative;
                    top: -.02rem;
                }
            }
            p{
                float: right;
            }
        }
    }
    .newsDetailsInfo{
        p{
            color: #333;
            font-size: .3rem;
            text-indent: 2em;
            text-align: justify;
            margin-top: .2rem;
        }
        img{
            max-width: 6.9rem;
            margin-top: .2rem;
        }
    }
}
.iframe{
    width: 7.5rem;
}
</style>
<style scoped>
.newsDetails{
   background: #fff;
}
.newsDetailsInfo >>> img{
    max-width: 6.9rem;
}  
.newsDetailsTop >>> strong{
    color: #000;
    font-weight: 800;
    font-size: .38rem;
} 
</style>


