<template>
    <div class="groupTcp">
      <div class="nav clearfix">
        <img class="navBack fl" src="../../../static/images/common/back.png" alt="" @click="navBack()">
        <h2 class="navTitle fl"></h2>
      </div>
      <div class="content">
        <h3></h3>
        <div class="msg">
          <p>() </p>
        </div>
        <div class="msg">
          <h4></h4>
          <p>1.</p>
          <p>2.</p>
          <p>3.</p>
          <h4></h4>
          <p>1.</p>
          <p>2.2APP</p>
          <p>3.</p>
          <h4></h4>
          <p>1.APP</p>
          <p>2.</p>
          <p>3.</p>
          <h4></h4>
          <p>1000</p>
          <h5></h5>
          <p>1.</p>
          <p>2.ID</p>
          <p>a)</p>
          <p>b)</p>
          <p>c)</p>
          <p>3.</p>
          <p></p>
          <h4></h4>
          <p>1.</p>
          <p></p>
        </div>
        <ul>
          <li><span class="xieyi" @click="agreeFun($event)"></span><b></b></li>
          <li><i>* </i>: <input type="text" v-model="tuandui" maxlength="30" @keyup="trim()"></li>
          <li><i>* </i><s :class="{'cur':tab===1}" @click="tab=1"></s><b style="margin-right: .4rem"></b> <s :class="{'cur':tab===2}" @click="tab=2"></s><b></b></li>
        </ul>
      </div>
      <div class="btn" v-if="agree==false || tuandui==''"></div>
      <div class="btn cur" v-if="agree==true && tuandui!=''" @click="commit()"></div>
    </div>
</template>

<script>
  import { Toast } from 'mint-ui';
    export default {
        name: "",
        data() {
            return {
              agree: true,//
              tab: 1,//
              tuandui: "",//
              tuanduiGo: "",
              xieyi: "",//
            }
        },
      mounted(){
          $(".xieyi").addClass("cur")
      },
        methods: {
          navBack() {
              this.$router.go(-1)
          },
          agreeFun(e){//
            if($(e.srcElement).hasClass("cur")){
              this.agree = false;
              $(e.srcElement).removeClass("cur")
            }else{
              this.agree = true;
              $(e.srcElement).addClass("cur")
            }
          },
          trim(){//
            this.tuandui=this.tuandui.replace(/(^\s+)|(\s+$)/g,'')
          },
          commit(){
            var that = this;

            this.$http({
              url: this.changeData() + "/team/auth/createTeam",
              method: 'post',
              params: {
                teamType: this.tab, //12
                teamName: this.tuandui,//
              }
            }).then(res => {
              console.log(res)
              console.log(this.tab)
              console.log(res.data.flag)
              if(res.data.code == "101"){
                if(this.tab == 2 && res.data.flag == 0){
                  that.$router.push({path:"/businessLicense",query:{url:"groupTcp"}})
                }else{
                  Toast('');
                  this.agree = false;
                  setTimeout(function () {
                    that.$router.push({path:"/develop"})
                  },2000)
                }
              }
            }).catch(err => {
              console.log(err)
            });
          }
        }
    }
</script>

<style lang="scss" type="text/scss" scoped>
  @import "../../../static/css/nav.css";
  .groupTcp{
    background: #fff;
    width: 7.5rem;
    margin: .9rem auto 0;
    .content{
      padding: .5rem .3rem 0;
      h3{
        text-align: center;
        font-size: .32rem;
        font-weight: bold;
        margin-bottom: .38rem;
      }
      .msg{
        margin-top: .5rem;
        p{
          font-size: .28rem;
          line-height: .5rem;
        }
        h4{
          font-size: .3rem;
          line-height: .5rem;
        }
      }
      ul{
        margin-top: .6rem;
        li{
          font-size: .28rem;
          margin-bottom: .44rem;
          input{
            font-size: .28rem;
          }
          span{
            display: inline-block;
            vertical-align: -.06rem;
            width: .34rem;
            height: .34rem;
            background: url(../../../static/images/code/yuan.png) no-repeat;
            -webkit-background-size: .34rem .34rem;
            background-size: .34rem .34rem;
            margin-right: .1rem;
            &.cur{
              background: url(../../../static/images/code/tongyi-bg.png) no-repeat;
              -webkit-background-size: .34rem .34rem;
              background-size: .34rem .34rem;
            }
          }
          b{
            display: inline-block;
          }
          i{
            color: #FF5343;
            margin-right: .1rem;
          }
          s{
            display: inline-block;
            vertical-align: -.04rem;
            width: .34rem;
            height: .34rem;
            background: url(../../../static/images/code/yuan.png) no-repeat;
            -webkit-background-size: .34rem .34rem;
            background-size: .34rem .34rem;
            margin-right: .1rem;
            &.cur{
              background: url(../../../static/images/code/tongyi-bg.png) no-repeat;
              -webkit-background-size: .34rem .34rem;
              background-size: .34rem .34rem;
            }
          }
          input{
            width: 1.88rem;
            border-bottom: 0.01rem solid #333;
          }
        }
      }
    }
    .btn{
      width: 7.5rem;
      height: .9rem;
      line-height: .9rem;
      color: #fff;
      text-align: center;
      font-size: .34rem;
      background: #ccc;
      &.cur{
        background: #64ABFF;
      }
    }
  }
</style>



